import type {
  MoFactory,
} from '@ali/4ever-cangjie';
import createSerializer from './serializer';
import { ELEMENT_TYPE } from './meta';

const factory: MoFactory = (configs) => ({
  key: 'paragraph',
  schemas: {
    type: ELEMENT_TYPE,
    isBlock: true,
  },
  serializer: createSerializer(configs),
  models: {
    klass: 'block',
    type: ELEMENT_TYPE,
    data: {
      ind: [undefined, Object],
      blockquote: [undefined, Boolean],
      spacing: [undefined, Object],
      jc: [undefined, String],
      calloutPr: [undefined, Object],
      subjectPr: [undefined, Object],
      uuid: [undefined, String],
      shd: [undefined, Object],
    },
  }
});

export default factory;
